/* animate to yellow for things that are selected-border */

@keyframes border-alertanimation-selected-1 {
	0% {border-color: var(--selected-border);}
	5% {border-color: #ffff77;}
	100% {border-color: var(--selected-border);}
}
@keyframes border-alertanimation-selected-2 {
	0% {border-color: var(--selected-border);}
	5% {border-color: #ffff77;}
	100% {border-color: var(--selected-border);}
}

/* animate to yellow for things that are unselected-border */

@keyframes border-alertanimation-unselected-1 {
	0% {border-color: var(--unselected-border);}
	5% {border-color: yellow;}
	100% {border-color: var(--unselected-border);}
}
@keyframes border-alertanimation-unselected-2 {
	0% {border-color: var(--unselected-border);}
	5% {border-color: yellow;}
	100% {border-color: var(--unselected-border);}
}



