.alertanimation-1 {
	animation-duration: .7s;
	animation-name: border-alertanimation-unselected-1;
}

.alertanimation-2 {
	animation-duration: .7s;
	animation-name: border-alertanimation-unselected-2;
}

.newselected.alertanimation-1 {
	animation-duration: .7s;
	animation-name: border-alertanimation-selected-1;
}

.newselected.alertanimation-2 {
